import { defineStore } from "pinia"

export const globalStore = defineStore('global-store', {
    state: () => ({
        navbarHeight: 0,
        navbarItem: [],
        scroll: 0,
    }),
    actions: {
        setNavbarHeight(height) {
            this.navbarHeight = height
        },
        setScroll(scroll) {
            this.scroll = scroll + (window.innerHeight * 0.40)
        },
        addNavbarItem(data) {
            this.navbarItem.push(data)
        }
    }
})