<template>
    <div>
        <div class="navbar-custom">
        <nav class="navbar navbar-expand-sm navbar-light">
            <div class="navbar-brand">{{ time }}</div>

            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbar">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbar">
                <ul class="navbar-nav ms-auto">
                    <li class="nav-item" v-for="(v,i) in navbarItem" :key="i"><a class="nav-link" href="javascript:void(0)" @click="scrollInto(v.offsetTop)">{{ v.name.toUpperCase() }}</a></li>
                </ul>
            </div>
        </nav>
    </div>
    </div>
</template>

<script>
import { mapActions, mapState } from 'pinia'
import { globalStore } from '@/stores/globalStore.js'
import moment from 'moment'

export default {
    name:"navbar",
    data() {
        return {
            time: '',
        }
    },
    computed: {
        ...mapState(globalStore, ['navbarItem', 'navbarHeight'])
    },
    methods: {
        ...mapActions(globalStore, ['setNavbarHeight']),
        scrollInto(offset) {
            window.scrollTo(0, offset - this.navbarHeight)
        }
    },
    mounted() {
        setInterval(() => {
            this.time = moment().format('dddd HH:mm:ss') 
        }, 1000)

        this.$nextTick(() => {
            this.setNavbarHeight(this.$el.getBoundingClientRect().height)
        })
    },
}
</script>

<style scoped lang="scss">
@import '../assets/css/navbar.scss';
</style>